/**
 * A class that can send out events.
 */
export default class EventDispatcher {
    /**
     * List of registered listeners
     */
    protected listeners: Object = {};

    /**
     * Add listener
     */
    public addEventListener(name: string, fn, thisObj: Object = null) {
        if (!this.listeners[name]) {
            this.listeners[name] = [];
        }
        this.listeners[name].push({fn: fn, thisObj: thisObj});
    }

    /**
     * Remove listener
     */
    public removeEventListener(name: string, fn = null, thisObj: Object = null) {
        if (!arguments.length) {
            this.listeners = {};
            return;
        }

        if (!this.listeners[name]) {
            return;
        }

        if (!fn) {
            this.listeners[name] = [];
        } else {
            let l = this.listeners[name];
            for (let i = l.length - 1; i >= 0; i--) {
                if (l[i].fn === fn && l[i].thisObj === thisObj) {
                    l.splice(i, 1);
                }
            }
        }
    }

    /**
     * Dispatch event.
     */
    public dispatchEvent(name: string, data = null, target = null) {

        let l = [].concat(this.listeners[name] || []);
        l = l.concat(this.listeners["*"] || []);

        if (!l.length) {
            return;
        }

        let e = {data: data, name: name, target: target || this};
        let fn;

        for (let i = 0; i < l.length; i++) {
            if (l[i].thisObj && typeof(l[i].fn) === "string") {
                fn = l[i].thisObj[l[i].fn];
            } else {
                fn = l[i].fn;
            }

            if (typeof(fn) === "function") {
                if (fn.apply(l[i].thisObj, [e]) === false) {
                    return false;
                }
            }
        }
    }

    /**
     * Forward event
     */
    public forwardEvent(e) {
        return this.dispatchEvent(e.name, e.data, e.target);
    }
}



