/**
 * The base class for all server APIs.
 */

/// <reference path="../../../../typings/tsd.d.ts" />

import Base from "../base";
import {MacroInterface, ServerApiInterface} from "./server-api-interface";

abstract class AbstractServerApi implements ServerApiInterface {
    /**
     * The base object
     */
    protected base: Base;

    constructor(base: Base) {
        this.base = base;
    }

    public abstract makeAdCall(macro: MacroInterface): JQueryPromise<void>;
}

export default AbstractServerApi;



