/// <reference path="../../../typings/tsd.d.ts" />

import Ad from "../lib/ad";
import IFrame from "../lib/iframe";
import * as $ from "jquery";
import Base from "../lib/base";

interface EventInterface {
    name: string;
    data: any;
    target: any;
}

// there are a few places in code where we need to iterate over all panels, so
// we define them here:
const panels = ["ft", "fr", "fb", "fl"];

/**
 * Skin ad (4 panels)
 */
export default class Skin extends Ad {
    protected response;
    protected _$anchor;

    protected _$ft;
    protected _$fr;
    protected _$fb;
    protected _$fl;

    // Flags of panels load checking
    protected _ftLoaded: boolean;
    protected _frLoaded: boolean;
    protected _fbLoaded: boolean;
    protected _flLoaded: boolean;

    // Cached jQuery window object
    protected _$win: JQuery;

    constructor(base: Base, response) {
        super(base, response.ad);
        this.response = response;
    }

    public serve() {
        // cache $(window):
        this._$win = $(window);

        // handle scrolling:
        this._$win.on("scroll.fe", () => {
            this.set({
                scrollLeft: this._$win.scrollLeft(),
                scrollTop: this._$win.scrollTop(),
            });
        }).scroll();

        // handle window resizes:
        this._$win.on("resize.fe", () => {
            this.set({
                windowWidth: this._$win.width(),
            });
        }).resize();

        // determine content height:
        this._autorun(this._updateContentHeight);

        // create the (reactive) frame, which updates with configuration:
        if (!(this._body() && this._anchor() && this._wrap())) {
            return false;
        }

        // load all panels:
        let iframe: IFrame;
        for (let i = 0; i < panels.length; i++) {
            iframe = this[panels[i]] = new IFrame({
                $container: this["_$" + panels[i]],
                name: panels[i],
                url: this.get([panels[i] + "Url"]),
            });

            iframe.addEventListener("*", (e) => {
                switch (e.name) {
                    case "INIT_COMPLETE":
                        this._onPanelInitComplete(e);
                        break;

                    default:
                        this.dispatchEvent(e.name, e.data);
                }
            });

            iframe.load({
                id: panels[i],
            });
        }
    }

    public unload() {
        this.listeners = {};

        this._$win.off("scroll.fe");

        if (this._$anchor) {
            this._$anchor.remove();
            delete this._$anchor;

            delete this._$ft;
            delete this._$fr;
            delete this._$fb;
            delete this._$fl;
        }

        this.dispatchEvent("UNLOADED");
    }

    /**
     * The creative is ready (i.e.: all panels have loaded).
     */
    protected _onReady() {
        // show it:
        this.set({visible: true});

        for (let i = 0; i < panels.length; i++) {
            this[panels[i]].callFunction("doStart");
        }

        // send scroll data to all panels:
        this._autorun(function () {
            if (this.get("sendScrollData")) {
                let scrollTop = this.get("scrollTop"),
                    offset;

                for (let i = 0; i < panels.length; i++) {
                    offset = this["_$" + panels[i]].offset();

                    this[panels[i]].callFunction("set", {
                        pageScrollTop: scrollTop,
                        scrollTop: scrollTop >= offset.top ? scrollTop - offset.top : 0,
                    });
                }
            }
        });

        this.dispatchEvent("SERVED");
    }

    /**
     * Manage the styles on document.body, used to make room for the skin when
     * content is fluid.
     */
    protected _body() {
        this._autorun(function () {
            let $body = $("body");

            // original body margins:
            if (!this._bodyMargin) {
                this._bodyMargin = {
                    "margin-left": parseInt($body.css("margin-left"), 10) || 0,
                    "margin-right": parseInt($body.css("margin-right"), 10) || 0,
                };
            }

            let contentWidth = this.get("contentWidth");
            if (contentWidth) {
                // content is fixed in size:
                let contentAlignment = this.get("contentAlignment");

                switch (contentAlignment) {
                    case "center":
                    case "centre":
                        $body.css({
                            "margin-left": this._bodyMargin["margin-left"] + "px",
                            "margin-right": this._bodyMargin["margin-right"] + "px",
                        });
                        break;

                    case "left":
                        let l = this.get("flSize") + this.get("flGutter");
                        $body.css({
                            "margin-left": Math.max(l, this._bodyMargin["margin-left"]) + "px",
                            "margin-right": this._bodyMargin["margin-right"] + "px",
                        });
                        break;
                }
            } else {
                // content is fluid, so make room for side frames using body margin:
                let l = this.get("flSize") + this.get("flGutter"),
                    r = this.get("frSize") + this.get("frGutter");

                $body.css($.extend({}, {
                    "margin-left": Math.max(l, this._bodyMargin["margin-left"]) + "px",
                    "margin-right": Math.max(r, this._bodyMargin["margin-right"]) + "px",
                }));
            }
        });

        return true;
    }

    /**
     * Create and manage the anchor element.
     */
    protected _anchor() {
        this._$anchor = $("<div></div>").css({
            "z-index": 10,
        }).prependTo("body");

        this._autorun(function () {
            let css: any = {
                height: this.get("ftSize") + "px",
                "margin-bottom": this.get("ftGutter") + "px",
            };

            if (this.get("visible")) {
                css.position = "relative";
                css.left = "initial";
            } else {
                css.position = "absolute";
                css.left = "-10000px";
            }

            let contentWidth = this.get("contentWidth");
            if (contentWidth) {
                css.width = contentWidth + "px";
            } else {
                css.width = "auto";
            }

            let contentAlignment = this.get("contentAlignment");
            switch (contentAlignment) {
                case "centre":
                case "center":
                    css["margin-left"] = "auto";
                    css["margin-right"] = "auto";
                    break;

                case "left":
                    css["margin-left"] = 0;
                    css["margin-right"] = 0;
                    break;
            }

            this._$anchor.css(css);
        });

        return true;
    }

    /**
     * Create and manage the frame.
     */
    protected _wrap() {
        for (let i = 0; i < panels.length; i++) {
            let panel = panels[i];
            this["_$" + panel] = $("<div></div>").css({
                overflow: "hidden",
            }).appendTo(this._$anchor);

           this._autorun(this["_" + panel + "Update"]);
        }

        return true;
    }

    /**
     * Update functions for each panel.
     */
    protected _ftUpdate(): void {
        let soT = this.get("soT"),
            fl = soT ? 0 : this.get("flSize") + this.get("flGutter"),
            fr = soT ? 0 : this.get("frSize") + this.get("frGutter"),
            anchorOffset = this._$anchor.offset(),
            panelOverlap = soT ? this.get("panelOverlap") : 0,
            // visible = this.get("visible"),
            // contentWidth = this.get("contentWidth"),
            // contentAlignment = this.get("contentAlignment"),
            css, r, l, w;

        // frame width:
        w = this._$anchor.width() + fl + fr + 2 * panelOverlap;

        // expand frame to cover all available space, if needed:
        if (!soT && this.get("ftExpand")) {
            // let windowWidth = this.get("windowWidth");

            // left side:
            fl = Math.max(fl, anchorOffset.left);

            // right side:
            let x = anchorOffset.left + this._$anchor.width() + this.get("frGutter"),
                available = this.get("windowWidth") - x;
            fr = Math.max(fr, available);

            w = Math.max(w, this._$anchor.width() + fl + fr);
        }

        // in case we might need to be fixed:
        if (this.get("ftFixed")) {
            let initialTop = anchorOffset.top;

            // determine where we should position relative to top edge of the window
            // when we are fixed:
            let fixedOffset = this.get("ftFixedOffset");
            if (isNaN(fixedOffset)) {
                fixedOffset = initialTop;
            }

            // threshold to switch to fixed positioning:
            let threshold = initialTop - fixedOffset;

            if (this.get("scrollTop") >= threshold) {
                l = anchorOffset.left - fl - panelOverlap;

                css = {
                    left: l + "px",
                    position: "fixed",
                    right: "auto",
                    top: fixedOffset + "px",
                    width: w + "px",
                };
            }
        }

        // we're not fixed:
        if (!css) {
            r = -(fr + panelOverlap);
            l = -(fl + panelOverlap);

            css = {
                left: l + "px",
                position: "absolute",
                right: r + "px",
                top: 0,
                width: "auto",
            };
        }

        $.extend(css, {
            height: this.get("ftSize") + "px",
            "z-index": (soT ? 1 : 3),
        });

        this._$ft.css(css);
    }

    protected _frUpdate(): void {
        let soT = this.get("soT"),
            soB = this.get("soB"),
            anchorOffset = this._$anchor.offset(),
            panelOverlapT = !soT ? this.get("panelOverlap") : 0,
            panelOverlapB = !soB ? this.get("panelOverlap") : 0,
            // visible = this.get("visible"),
            // contentWidth = this.get("contentWidth"),
            // contentAlignment = this.get("contentAlignment"),
            css, t, r, l, h, w;

        // frame height:
        h = this.get("contentHeight");
        h += (soT ? this.get("ftSize") : 0) + (soB ? this.get("fbSize") : 0);
        h += panelOverlapT + panelOverlapB;

        // frame width:
        w = this.get("frSize");

        // expand frame to cover all available space, if needed:
        if (this.get("frExpand")) {
            let x = anchorOffset.left + this._$anchor.width() + this.get("frGutter"),
                available = this.get("windowWidth") - x;

            w = Math.max(w, available);
        }

        // in case we might need to be fixed:
        if (this.get("frFixed")) {
            let initialTop = anchorOffset.top + (soT ? 0 : this.get("ftSize"));

            // determine where we should position relative to top edge of the window
            // when we are fixed:
            let fixedOffset = this.get("frFixedOffset");
            if (isNaN(fixedOffset)) {
                fixedOffset = initialTop;
            }

            // threshold to switch to fixed positioning:
            let threshold = initialTop - fixedOffset + panelOverlapT;

            if (this.get("scrollTop") >= threshold) {
                l = anchorOffset.left + this._$anchor.width() + this.get("frGutter");

                css = {
                    left: l + "px",
                    position: "fixed",
                    right: "auto",
                    top: (fixedOffset - panelOverlapT) + "px",
                };
            }
        }

        // we're not fixed:
        if (!css) {
            t = (soT ? 0 : this.get("ftSize")) - panelOverlapT;
            r = -(w + this.get("frGutter"));

            css = {
                left: "auto",
                position: "absolute",
                right: r + "px",
                top: t + "px",
            };
        }

        $.extend(css, {
            height: h + "px",
            width: w + "px",
            "z-index": 2,
        });

        this._$fr.css(css);
    }

    protected _fbUpdate(): void {
        let soB = this.get("soB"),
            fl = soB ? 0 : this.get("flSize") + this.get("flGutter"),
            fr = soB ? 0 : this.get("frSize") + this.get("frGutter"),
            anchorOffset = this._$anchor.offset(),
            ftSize = this.get("ftSize"),
            ftGutter = this.get("ftGutter"),
            fbGutter = this.get("fbGutter"),
            contentHeight = this.get("contentHeight"),
            panelOverlap = soB ? this.get("panelOverlap") : 0,
            // visible = this.get("visible"),
            // contentWidth = this.get("contentWidth"),
            // contentAlignment = this.get("contentAlignment"),
            css, t, r, l;

        // expand frame to cover all available space, if needed:
        if (!soB && this.get("fbExpand")) {
            // left side:
            fl = Math.max(fl, anchorOffset.left);

            // right side:
            let x = anchorOffset.left + this._$anchor.width() + this.get("frGutter"),
                available = this.get("windowWidth") - x;
            fr = Math.max(fr, available);
        }

        t = ftSize + ftGutter + contentHeight + fbGutter;
        r = -(fr + panelOverlap);
        l = -(fl + panelOverlap);

        css = {
            height: this.get("fbSize") + "px",
            left: l + "px",
            position: "absolute",
            right: r + "px",
            top: t + "px",
            "z-index": (soB ? 1 : 3),
        };

        this._$fb.css(css);
    }

    protected _flUpdate(): void {
        let soT = this.get("soT"),
            soB = this.get("soB"),
            anchorOffset = this._$anchor.offset(),
            panelOverlapT = !soT ? this.get("panelOverlap") : 0,
            panelOverlapB = !soB ? this.get("panelOverlap") : 0,
            // visible = this.get("visible"),
            // contentWidth = this.get("contentWidth"),
            // contentAlignment = this.get("contentAlignment"),
            css, t, l, h, w;

        // frame height:
        h = this.get("contentHeight");
        h += (soT ? this.get("ftSize") : 0) + (soB ? this.get("fbSize") : 0);
        h += panelOverlapT + panelOverlapB;

        // frame width:
        w = this.get("flSize");

        // expand frame to cover all available space, if needed:
        if (this.get("flExpand")) {
            // although we don"t use this value, we need it to create a dependency:
            this.get("windowWidth");

            w = Math.max(w, anchorOffset.left);
        }

        // in case we might need to be fixed:
        if (this.get("flFixed")) {
            let initialTop = anchorOffset.top + (soT ? 0 : this.get("ftSize"));

            // determine where we should position relative to top edge of the window
            // when we are fixed:
            let fixedOffset = this.get("flFixedOffset");
            if (isNaN(fixedOffset)) {
                fixedOffset = initialTop;
            }

            // threshold to switch to fixed positioning:
            let threshold = initialTop - fixedOffset + panelOverlapT;

            if (this.get("scrollTop") >= threshold) {
                l = anchorOffset.left - (w + this.get("flGutter"));

                css = {
                    left: l + "px",
                    position: "fixed",
                    top: (fixedOffset - panelOverlapT) + "px",
                };
            }
        }

        // we're not fixed:
        if (!css) {
            t = (soT ? 0 : this.get("ftSize")) - panelOverlapT;
            l = -(w + this.get("flGutter"));

            css = {
                left: l + "px",
                position: "absolute",
                top: t + "px",
            };
        }

        $.extend(css, {
            height: h + "px",
            width: w + "px",
            "z-index": 2,
        });

        this._$fl.css(css);
    }

    /**
     * Update content height.
     */
    protected _updateContentHeight() {
        this.set({
            contentHeight: Math.max(
                0, this._getPageHeight() + this.get("contentHeightAdjustment")
            ),
        });
    }

    /**
     * Calculate page height.
     */
    protected _getPageHeight() {
        // check if we have a hardcoded value in integration parameters:
        let h = this.get("contentHeightFixed");
        if (h) {
            return h;
        }

        // calculate based on the configured method:
        switch (this.get("contentHeightComputeVersion")) {
            case 2:
                return this._getPageHeightV2();

            default:
                return this._getPageHeightV1();
        }
    }

    /**
     * Calculate page height by taking the max between body height and the top
     * offset of a marker element we insert as the last child on the body.
     */
    protected _getPageHeightV1() {
        let $body = $("body");

        // body height:
        let bodyH = $body.height();

        // place a marker element and get its offset position:
        let $marker = $("<div></div>").css({
            "width": "1px",
            "height": "1px",
        }).appendTo($body);
        let marker_offset = $marker.offset();

        // remove marker:
        $marker.remove();

        // account for body top margin/border/padding:
        let bodyM = parseInt($body.css("margin-top"), 10) || 0;
        let bodyB = parseInt($body.css("border-top"), 10) || 0;
        let bodyP = parseInt($body.css("padding-top"), 10) || 0;

        // height:
        let h = Math.max(bodyH, marker_offset.top - (bodyM + bodyB + bodyP));

        // substract our contribution to this height:
        if (this._$anchor) {
            h -= this.get("ftSize") + this.get("ftGutter");
        }

        return Math.round(h);
    }

    /**
     * Calculate page height by looking at the whole document height and
     * substracting our contribution to that height (this method doesn't support
     * pages that decrease in size).
     */
    protected _getPageHeightV2() {
        let $body = $("body");

        // document height
        let h = $(document).height();

        // account for body top margin/border/padding
        let bodyM = parseInt($body.css("margin-top"), 10) || 0;
        let bodyB = parseInt($body.css("border-top"), 10) || 0;
        let bodyP = parseInt($body.css("padding-top"), 10) || 0;

        h -= (bodyM + bodyB + bodyP);

        // substract our contribution to this height
        if (this._$anchor) {
            h -= (
                this.get("ftSize") + this.get("ftGutter")
                +
                this.get("fbSize") + this.get("ftGutter")
            );
        }

        return Math.round(h);
    }

    /**
     * Default configuration.
     */
    protected _defaults() {
        return {
            contentAlignment: "center",
            contentHeight: 0,
            contentHeightAdjustment: 0,
            contentHeightComputeVersion: 1,
            contentHeightFixed: 0,
            contentWidth: 0,
            flFixedOffset: null,
            frFixedOffset: null,
            ftFixedOffset: null,

            // the amount by which we overlap panels to mitigate seams at common
            // edges:
            panelOverlap: 1,

            // internal state:
            scrollLeft: 0,
            scrollTop: 0,
            windowWidth: 0,
        };
    }

    /**
     * List the keys in configuration which need to be either booleans or
     * integers.
     */
    protected _booleans(): Array<string> {
        return ["ftFixed", "ftExpand", "frFixed", "frExpand", "fbExpand", "flFixed", "flExpand"];
    }

    protected _integers(): Array<string> {
        return ["contentHeight", "contentWidth", "ftFixedOffset", "frFixedOffset", "flFixedOffset"];
    }

    /**
     * Handle INIT_COMPLETE events from the panels.
     */
    private _onPanelInitComplete(e: EventInterface) {
        this["_" + e.target.cfg.name + "Loaded"] = true;

        // once all panels have loaded, show the creative:
        if (this._ftLoaded && this._frLoaded && this._fbLoaded && this._flLoaded) {
            this._onReady();
        }
    }
}



