/**
 * Simple logging to console.
 */
export default class Log {
    /**
     * default logging level (surpress all logs except the ones with 0 level)
     *
     * @type {number}
     * @private
     */
    private static _level: number = 0;

    /**
     * Set/get logging level
     *
     * @param level
     * @returns {number}
     */
    public static level(level: number) {
        if (level !== undefined) {
            this._level = level;
        }

        return this._level;
    }

    public static error() {
        this._log(1, arguments);
    }

    public static warning() {
        this._log(2, arguments);
    }

    public static info() {
        this._log(3, arguments);
    }

    public static debug() {
        this._log(4, arguments);
    }

    public static trace() {
        this._log(5, arguments);
    }

    public static log(name: string, data: any) {
        this._log(0, arguments);
    }

    /**
     * Generic function to log something
     *
     * @param level
     * @param args
     * @private
     */
    private static _log(level: number, ...args: Array<any>) {
        if (level > this._level) {
            return;
        }

        if (!console || typeof(console.log) !== "function") {
            return;
        }

        let type = (["Log", "Error", "Warning", "Info", "Debug", "Trace"])[level];
        args.unshift("[FE][" + type + "]:");

        console.log.apply(console, args);
    };
}



