/// <reference path="../../../typings/tsd.d.ts" />

import EventDispatcher from "./event-dispatcher";
import Base from "./base";
import Util from "./util";
import * as $ from "jquery";
import * as meteorReactive from "meteor-reactive";

/**
 * The base class for all ads
 */
export default class Ad extends EventDispatcher {
    protected _cfg;
    protected meteorReactive: MeteorReactiveInterface;

    /**
     * The base object
     */
    private base: Base;
    private _trackers: Object;

    constructor(base: Base, cfg) {
        super();

        this.meteorReactive = meteorReactive;
        this.base = base;
        this.set(cfg);
    }

    public unload() {
        if (this._trackers) {
            for (let k in this._trackers) {
                if (this._trackers.hasOwnProperty(k)) {
                    this._trackers[k].stop();
                    delete this._trackers[k];
                }
            }
        }
    }

    /**
     * Run a dependency aware function, which will rerun itself whenever one of
     * its dependencies change.
     */
    protected _autorun(fn: any, name: string = null): void {
        if (!name) {
            name = "autorun_" + Math.floor(Math.random() * 10e16);
        }

        if (!this._trackers) {
            this._trackers = {};
        }

        this._trackers[name] = this.meteorReactive.Tracker.autorun(() => {
            fn.apply(this, []);
        });
    }

    /**
     * Set configuration options.
     */
    public set(kv) {
        let _kv = {};

        if (!this._cfg) {
            this._cfg = new this.meteorReactive.ReactiveDict();

            $.extend(true,
                _kv,
                // default configuration for this ad type:
                this._defaults(),
                // passed configuration:
                kv
            );

            // apply integration configuration (only known keys):
            let p = this.base.get();
            for (let k in p) {
                if (_kv[k] === undefined) {
                    delete p[k];
                }
            }
            $.extend(true, _kv, p);
        } else {
            $.extend(true, _kv, kv);
        }

        // make sure parameters are of the correct type:
        Util.cast(_kv, "boolean", this._booleans());
        Util.cast(_kv, "integer", this._integers());

        for (let k in _kv) {
            if (_kv.hasOwnProperty(k)) {
                this._cfg.set(k, _kv[k]);
            }
        }
    }

    /**
     * Get configuration options
     */
    public get (...args: Array<any>) {
        if (args.length) {
            // get specific configuration keys:
            if (args.length === 1) {
                return this._cfg.get(args[0]);
            } else {
                let kv = {};
                for (let i = 0; i < args.length; i++) {
                    kv[args[i]] = this._cfg.get(args[i]);
                }
                return kv;
            }
        } else {
            // get all keys
            return this._cfg.all();
        }
    }

    /**
     * Default configuration (overload in child class).
     */
    protected _defaults(): Object {
        return {};
    }

    /**
     * Define boolean fields in configuration (overload in child class).
     */
    protected _booleans(): Array<any> {
        return [];
    }

    /**
     * Define integer fields in configuration (overload in child class).
     */
    protected _integers(): Array<any> {
        return [];
    }
}



