/// <reference path="../../../../typings/tsd.d.ts" />

import * as $ from "jquery";
import AbstractServerApi from "./abstract-server-api";
import {MacroInterface} from "./server-api-interface";
import Base from "../base";
// import Log from "./../log";

interface EventTrackerInterface {
    tag: string;
    url: string;
    customId: number;
    reportingName: string;
    landingPageUrl: string;
    silent: boolean;
}

interface CustomEventInterface {
    urls: Array<string>;
    landingPageUrl: string;
    silent: boolean;
}

interface AdzerkEventInterface {
    id: number;
    url: string;
}

/**
 * Retrieve the ad from an AdZerk API
 */
export default class Native extends AbstractServerApi {
    protected _callback: any;

    protected flightId: number;

    protected viewTime: number;

    protected trackers: {
        impression: Array<string>,
        viewableImpression: string,
        click: Array<string>,
        event: {[tag: string]: CustomEventInterface}
    } = { click: [], event: {}, impression: [], viewableImpression: ""};

    constructor(base: Base) {
        super(base);

        // Grab the current view time
        this.viewTime = Math.round(new Date().getTime() / 1000);

        this._callback = this.adzerkDecision;

        // listen to events from the ad (through the base object):
        this.base.addEventListener("*", (e) => {
            switch (e.name) {
                case "AD_LOADED":
                    // Serve impressions.
                    // Ad has been loaded, but not yet visible
                    for (let url of this.trackers.impression) {
                        $.ajax(url, {crossDomain: true, type: "GET"});
                    }
                    break;
                case "AD_SERVED":
                    // Serve viewable impression.
                    // All panels are ready and viewed.
                    $.ajax(this.trackers.viewableImpression, {crossDomain: true, type: "GET"});
                    this.frequencyCapping();
                    break;
                default:
                    // Serve custom event.
                    this.fireTracker(e.name, e.data);
                    break;
            }
        });
    }

    /**
     *
     * @param macro
     * @returns {JQueryPromise<*>}
     */
    public makeAdCall(macro: MacroInterface): JQueryPromise<void> {
        let url = "http://engine.adzerk.net/api/v2";
    //    let flightId: string = macro.srv_AdvertIDs ? `"flightId": ${macro.srv_AdvertIDs},` : "";
        let userKey: string = this.getCookie("user") ? `"user": { "key": "${this.getCookie("user")}" },` : "";
        let adViews: string = this.getCookie("adViews") ? `"flightViewTimes": ${this.getCookie("adViews")},` : "";
        let eventIds = [
            40, // Viewable Impression
            104, 105, 106, 107, 108, 109, 110,
            201, 202, 203, 204, 205, 206, 207, 208, 209, 210,
        ];

        // Add impression tracker URL form as tag
        if (macro.plr_ImpressionURL) {
            this.trackers.impression.push(macro.plr_ImpressionURL);
        }

        // Add click tracker URL form as tag
        if (macro.plr_ClickURL) {
            this.trackers.click.push(macro.plr_ClickURL);
        }

        return $.ajax(url, {
            crossDomain: true,
            // data: `{
            //         ${userKey}
            //         ${adViews}
            //         "time": ${this.viewTime},
            //         "placements": [{
            //         "divName": "ad",
            //         "siteId": ${macro.srv_SectionID},
            //         ${flightId}
            //         "adTypes": [163],
            //         "eventIds": [${eventIds.toString()}]
            // }]}`,
            data: `{
                    ${userKey}
                    ${adViews}
                    "time": ${this.viewTime},
                    "placements": [{
                    "divName": "ad",
                    "siteId": ${macro.srv_SectionID},
                    "adTypes": [5],
                    "eventIds": [${eventIds.toString()}]
            }]}`,
            dataType: "json",
            type: "POST",
        }).then((response) => {
            return this._callback(response);
        });
    }

    set callback(callback: (n: number) => any) {
        this._callback = callback ? callback : this.adzerkDecision;
    }

    get callback(): (n: number) => any {
        return this._callback;
    }

    protected adzerkDecision(response): void {
        this.saveUserIdInCookie(response.user.key);

        // Check if Adzerk response meets minimum requirements
        if (!this.checkNested(response, "decisions", "ad", "contents")) {
            return;
        }

        this.flightId =  response.decisions.ad.flightId;
        let json = response.decisions.ad.contents[0];

        if (response.decisions.ad.impressionUrl) {
            this.trackers.impression.push(response.decisions.ad.impressionUrl);
        }

        if (response.decisions.ad.clickUrl) {
            this.trackers.click.push(response.decisions.ad.clickUrl);
        }

        if (json.data.customData && json.data.customData.events && response.decisions.ad.events) {
            this.collectTrackers(json.data.customData.events, response.decisions.ad.events);
        }

        if (this.base.get("autopilot") && json.data.customData) {
            this.base.serve(json.data.customData);
            this.base.dispatchEvent("AD_LOADED");
        }

        // If response has an executable script body - evaluate it
        if (json.body) {
            this._evalScriptBody(json.body);
        }
    }

    /**
     * Collect Custom events from Manifest and map them to Event trackers, provided by AdZerk
     *
     * @param customEvents
     * @param adzerkEvents
     */
    private collectTrackers(customEvents: Array<EventTrackerInterface>, adzerkEvents: Array<AdzerkEventInterface>) {
        // Initialize custom events list
        this.trackers.event = {};

        // Reformat AdZerk trackers into associative array [id: url]
        let formattedAdzerkEvents: { [id: number]: string } = {};
        for (let event of adzerkEvents) {
            if (event.id === 40) {
                // Tracker with ID of 40 is a Viewable impression
                this.trackers.viewableImpression = event.url;
            } else {
                formattedAdzerkEvents[event.id] = event.url;
            }
        }

        for (let event of customEvents) {
            if ("IMPRESSION_TRACKER" === event.tag) {
                // Events with "IMPRESSION_TRACKER" tag served separately
                this.trackers.impression.push(event.url);
            } else {
                this.trackers.event["AD_" + event.tag] = {
                    "urls": [event.url, formattedAdzerkEvents[event.customId]],
                    "landingPageUrl": event.landingPageUrl,
                    "silent": event.silent,
                };
            }
        }
    }

    /**
     * Fire custom event tracker
     *
     * @param name
     * @param data
     */
    private fireTracker(name: string, data: any) {
        if (this.trackers.event[name]) {
            // Fire all trackers, registered for current event
            for (let url of this.trackers.event[name].urls) {
                $.ajax(url, {crossDomain: true, type: "GET"});
            }

            // Fire all click events if no silent option is set
            if (!this.trackers.event[name].silent && this.trackers.click) {
                for (let url of this.trackers.click) {
                    $.ajax(url, {crossDomain: true, type: "GET"});
                }
            }

            // If event has landingPageUrl parameter - open the URL in a new browser tab
            if (this.trackers.event[name].landingPageUrl) {
                let win = window.open(this.trackers.event[name].landingPageUrl, "_blank");
                win.focus();
            }
        }

        // Log.log(name, data);
    }

    private frequencyCapping() {
        // Create a new hash with empty views array
        let hash = {};
        hash[this.flightId] = [];
        // Check if hash already exists in Cookies
        let adViews = this.getCookie("adViews");

        if (adViews) {
            // If hash found in Cookies - remove old view timestamps (older than 60 days)
            hash = JSON.parse(adViews);
            hash[this.flightId] =  hash[this.flightId].filter((date) => {
                return this.viewTime - 60 * 60 * 24 * 60 < date;
            });
        }

        hash[this.flightId].push(this.viewTime);
        // Save updated hash to the Cookie
        this.saveViewTimeInCookie(hash);
    }

    /**
     * Check if JSON structure is valid
     *
     * @param obj
     * @param args
     * @returns {boolean}
     */
    private checkNested(obj, ...args: Array<string>) {
        for (let i = 0; i < args.length; i++) {
            if (!obj || !obj.hasOwnProperty(args[i])) {
                return false;
            }
            obj = obj[args[i]];
        }

        return true;
    }

    private saveUserIdInCookie(user) {
        document.cookie = `user=${user}; path=/`;
    }

    private saveViewTimeInCookie(hash) {
        let adViews = JSON.stringify(hash);
        document.cookie = `adViews=${adViews}; path=/`;
    }

    /* tslint:disable:no-eval */
    /**
     * Evaluate script from script body
     *
     * @param code
     * @private
     */
    private _evalScriptBody(code: string) {
        try {
           //  console.log(this._callbackFunction);
           //  console.log(this._callbackFunction(code));
           // // let fn = window[this._callbackFunction];
           //  this._callbackFunction(code);
           //  Strip all HTML tags (which are <script> tags)
           //  eval(code.replace(/(<([^>]+)>)/ig, ""));
           //  let regex = /<script.*?src="(.*?)"/;
           //  let src = regex.exec(code)[1];
           //
           //  console.log(src);



            let iframe = document.createElement("iframe");
            iframe.width = "320px";
            iframe.height = "270px";
            let html = "<body>" + code + "</body>";
            iframe.src = "data:text/html;charset=utf-8," + encodeURI(html);
            iframe.scrolling = "no";
            iframe.frameBorder = "0";
            document.getElementById("targ").appendChild(iframe);
        } catch (e) {
            if (e instanceof SyntaxError) {
                console.log(e.message);
            }
        }
    }
    /* tslint:enable:no-eval */

    private getCookie(name) {
        let matches = document.cookie.match(new RegExp(
            "(?:^|; )" + name.replace(/([\.$?*|{}\(\)\[\]\\\/\+^])/g, "\\$1") + "=([^;]*)"
        ));
        return matches ? decodeURIComponent(matches[1]) : undefined;
    }
}



