/// <reference path="../../../typings/tsd.d.ts" />

import EventDispatcher from "./event-dispatcher";
import ads from "../ads";
import Ad from "./ad";
import log from "./log";
import * as serverApis from "./server-apis";
import Util from "./util";
import * as $ from "jquery";
import * as meteorReactive from "meteor-reactive";

/**
 * The main class (FE.Base)
 */
export default class Base extends EventDispatcher {

    protected _swInitCalled: boolean;
    protected _ready: boolean;
    protected _cfg;
    protected _overlay;
    protected _ad: Ad;
    protected _serverApis: Object;
    protected reactiveDict;

    constructor(cfg = null) {
        super();
        this.reactiveDict = meteorReactive.ReactiveDict;
        // set configuration:
        this.set(cfg);

        // initialise logging:
        log.level(this.get("DEBUG"));
    }

    /**
     * Initialise: wait for document.body to become available and load an ad.
     */
    public init() {
        // wait until document.body is available:
        if (!document.body) {
            setTimeout(() => this.init(), 25);
            return;
        }

        // don't call this function more than once:
        if (this._swInitCalled) {
            return;
        }
        this._swInitCalled = true;

        // we are ready for business:
        this._ready = true;
        this.dispatchEvent("ready");

        // when autopilot is enabled, we make an initial ad call or load an ad if
        // one was passed to us:
        if (this.get("autopilot")) {
            // in case we were provided an ad call response, serve the ad within it:
            let adCallResponse = this.get("adCallResponse");
            if (adCallResponse) {
                this.serve(adCallResponse);
            } else {
                // otherwise make an ad call
                this.makeAdCall();
            }
        }
    }

    /**
     * Make an ad call. Returns promise which will be resolved when the ad call is
     * complete.
     */
    public makeAdCall() {
        let serverApi = this.get("serverApi");

        // Dirty hotfix to skip second request
        if (!serverApi) {
            return;
        }

        // create the instance, if it"s not already there:
        if (!this._serverApis) {
            this._serverApis = {};
        }

        if (!this._serverApis[serverApi]) {
            this._serverApis[serverApi] = new serverApis[serverApi](this);
        }

        this._serverApis[serverApi].makeAdCall(window[this.get("tag")]);
    }

    /**
     * Serve an ad.
     */
    public serve(response) {
        if (!response) {
            return;
        }

        let ad = null;
        switch (response.ad.type) {
            case "skin":
                ad = new ads.Skin(this, response);
                break;
        }

        if (ad) {
            this._ad = ad;

            ad.addEventListener("*", (e) => {
                this.dispatchEvent("AD_" + e.name, e.data, e.target);
            });

            ad.serve();
        }
    }

    /**
     * Unload the current ad, if any
     */
    public unload() {
        if (this._ad) {
            this._ad.removeEventListener("*");
            this._ad.unload();
            this._ad = null;
        }
    }

    /**
     * Open an overlay
     */
    public openOverlay(cfg) {
        // only allow one overlay at a time:
        if (this._overlay) {
            return;
        }

        this._overlay = new ads.Overlay(this, cfg);

        this._overlay.addEventListener("*", (e) => {
            this.forwardEvent(e);

            switch (e.name) {
                case "CLOSED":
                    this._overlay = null;
                    break;
            }
        });

        this._overlay.serve();
    }

    /**
     * Close the active overlay
     */
    public closeOverlay() {
        if (this._overlay) {
            this._overlay.unload();
            this._overlay = null;
        }
    }

    /**
     * Set configuration options
     */
    public set(kv) {
        let _kv = {};

        if (!this._cfg) {
            this._cfg = new this.reactiveDict();

            $.extend(_kv,
                // default configuration
                {
                    // debug level:
                    DEBUG: 0,

                    // static URL to get an ad (instead of going through an ad server)
                    adCallResponseUrl: "",

                    // static ad call result
                    adCallResult: null,

                    // what ad server interface/API should we use
                    adServerType: "",

                    // autopilot mode - if enabled, we will make an ad call on init, or
                    // automatically serve the ad after an ad call; if disabled, all steps
                    // need to be manually initiated
                    autopilot: true,
                },
                // passed configuration
                kv,
                // configuration passed in the URL
                Util.getUrlParams(null, true)
            );
        } else {
            $.extend(_kv, kv);
        }

        // make sure parameters are of the correct type:
        Util.cast(_kv, "boolean", []);
        Util.cast(_kv, "integer", ["DEBUG"]);

        for (let k in _kv) {
            if (_kv.hasOwnProperty(k)) {
                this._cfg.set(k, _kv[k]);
            }
        }
    }

    /**
     * Get configuration options
     *
     * @param args
     * @returns {any}
     */
    public get (...args: Array<string>) {
        if (args.length) {
            // get specific configuration keys:
            if (args.length === 1) {
                return this._cfg.get(args[0]);
            } else {
                let kv = {};
                for (let i = 0; i < args.length; i++) {
                    kv[args[i]] = this._cfg.get(args[i]);
                }
                return kv;
            }
        } else {
            // get all keys
            return this._cfg.all();
        }
    }
}



