/// <reference path="../../../typings/tsd.d.ts" />

import Ad from "../lib/ad";
import * as $ from "jquery";

/**
 * Overlay
 */
class Overlay extends Ad {
    private _$backdrop;

    public serve() {
        this._backdrop().then(() => {
            // if configured, close when clicking on the backdrop:
            if (this.get("closeOnBackdropClick")) {
                this._$backdrop.click(function () {
                    this.unload();
                });
            }

            // if configured, close when pressing escape:
            if (this.get("closeOnEscape")) {
                $(document).on("keyup.fe", (e) => {
                    if (e.keyCode === 27) {
                        this.unload();
                    }
                });
            }
        });
    }

    public unload() {
        this.listeners = {};

        $(document).off("keyup.fe");

        this._$backdrop.remove();
        this.dispatchEvent("CLOSED");
    }

    /**
     * Create a backdrop (which may be transparent). Returns promise resolved when
     * the backdrop is fully displayed.
     */
    private _backdrop() {
        let deferred = $.Deferred(),
            transparent = this.get("transparentBackdrop");

        this._$backdrop = $("<div></div>").css({
            "background-color": transparent ? "transparent" : "#000",
            bottom: 0,
            left: 0,
            opacity: 0.1,
            position: "fixed",
            right: 0,
            top: 0,
            "z-index": 20,
        }).animate({
            opacity: transparent ? 1 : 0.8,
        }, transparent ? 0 : 750, function () {
            deferred.resolve();
        }).appendTo("body");

        return deferred.promise();
    }
}

export default Overlay;



